@import url(https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body{
  font-family: 'Oxygen', sans-serif; 
  color: #fff;
}
.container{
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 500px;
  border-radius: 5px;
  background-image: url(/task-tracker/static/media/bg.230a8879.jpg);
  background-repeat: repeat-y;
}
.header{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin: 10px 10px 20px 10px;
  height: 40px;
  color: #fff
  
}
.btn{
  display: inline-block;
  background: black;
  color: white;
  border: none;
  outline: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
  transition: -webkit-transform 450ms;
  transition: transform 450ms;
  transition: transform 450ms, -webkit-transform 450ms;
}
.btn:hover{
  outline: none;
}
.btn:active{
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}
.btn-block{
  display: block;
  width: 100%;
}

.task{
  background: transparent;
  margin: 5px;
  padding: 10px 20px;
}
.task.reminder{
  border-left: 5px solid green;
}
.task h3 {
display: -webkit-flex;
display: flex;
-webkit-align-items: center;
        align-items: center;
-webkit-justify-content: space-between;
        justify-content: space-between;
color: #fff;
}

.add-form{
  margin-bottom: 40px;
}
.form-control {
  margin: 20px 0;
}
.form-control label {
  display: block;
}
.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
  background: transparent;
  outline: none;
border: none;
border-bottom: 1px solid #fff;
color: #fff;
}
.form-control-check {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.form-control-check label {
  -webkit-flex: 6 1;
          flex: 6 1;
  margin-left: 40px;
}
.form-control-check input {
  -webkit-flex: 2 1;
          flex: 2 1;
  height: 20px;
}
footer {
  margin-top: 30px;
  text-align: center;
}
